.home{
    width:100%;
    height: fit-content;
    margin-top: auto;
    margin-bottom: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-self: center;
    z-index:9990;
}

.home__middleLogo{
    width:100%;
}

.home__middle{
    width:100%;
    height:20%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.home__middleAnchor{
    width:10%;
    height:100%;
    min-width:150px;
}

.home__question{
    width:fit-content;
    margin: 0 auto 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
p{
    margin:0;
    padding: 0;
    font-size: 202%;
}

.home__questionDiv{
    width:fit-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-self: center;
}

.home__serviceContactDiv{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.left{
    padding-right:10px
}
.right{
    padding-left:10px;
}
.home__serviceContact{
    margin-top:10px;
}

@keyframes rotation {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(359deg);
    }
  }

.home__middleLogo{
    animation: rotation 8s linear infinite;
}


@media only screen and (max-width: 500px) {
    .home__serviceContact{
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .home__serviceContactDiv{
        width:fit-content;
        margin-left:auto;
        margin-right: auto;
    }
  }

@media only screen and (min-width: 500px){
    .home{
        padding-top:20px;
    }
    .home__serviceContact{
        display: flex;
        flex-direction: row;
        justify-content: center;
    }

    .home__serviceContactDiv{
        width:fit-content;
    }
    .left{
        align-self: right;
    }
    .right{
        align-self:left; 
    }
}


@keyframes tumble{
    0% {transform : rotate(0deg)}
    10%{transform: rotate(3deg)}
    20%{transform: rotate(6deg)}
    30%{transform: rotate(3deg)}
    40%{transform: rotate(0deg);}
    50%{transform: rotate(-3deg);}
    60%{transform: rotate(-6deg);}
    70%{transform: rotate(-3deg);}
    80%{transorm:rotate(0deg);}
}



.home__question:hover, .left:hover, .right:hover{
    animation: tumble 2s  linear;
}
