.bg{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

@keyframes shift{
    0%{transform : translateX(0px)}
    10%{transform: translateX(2px)}
    20%{transform:translateX(4px)}
    40%{transform: translateX(6px)}
    60%{transform: translate(4px)}
    80%{transform: translate(2px)}
    100%{transform: translate(0px)}

}
@keyframes shift2{
    0%{transform : translateX(0px)}
    10%{transform: translateX(-2px)}
    20%{transform:translateX(-4px)}
    40%{transform: translateX(-6px)}
    60%{transform: translate(-4px)}
    80%{transform: translate(-2px)}
    100%{transform: translate(0px)}

}

.level1{
    
    background-color: #7ed957;
    z-index: -1;
    position: fixed;
    animation: shift 2s linear infinite;
    animation-delay: 0s;
}


.level2{
    
    background-color: #94f16c;
    animation: shift 2s linear infinite;
    animation-delay: 1s;
}
.level3{
 
    background-color: #c0ffac;
    animation: shift 2s linear infinite;
    animation-delay: 0s;
}
.level4{
   
    background-color: #e9ffe0;
    animation: shift 2s linear infinite;
    animation-delay: 1s;
}


.bg{
    background-size: contain;
    border-radius: 50px;
}

.bg{
    opacity:0.7;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}


@media only screen and (max-width:500px){
    .second{
        right:-250px;
        bottom:-100px;

    }
    
    .level1{
        width:400px;
        height:400px;
        left:-250px;
        top:-100px;
        border-radius: 400px;
    }
    
    .level2{
        width:350px;
        height:350px;
        border-radius: 300px;
    }
    .level3{
        width:300px;
        height:300px;
        border-radius: 200px;
    }
    .level4{
        width:250px;
        height:250px;
        border-radius: 250px;
    }
    .level5{
        width:300px;
        height:300px;
        border-radius: 100px;

    }

}


@media only screen and (min-width:500px){
    .second{
        right:-50px;
        bottom:-10px;

    }
    
    .level1{
        width:600px;
        height:600px;
        left:-150px;
        top:-10px;
        border-radius: 600px;
    }
    
    .level2{
        width:550px;
        height:550px;
        border-radius: 500px;
    }
    .level3{
        width:450px;
        height:450px;
        border-radius: 400px;
    }
    .level4{
        width:400px;
        height:400px;
        border-radius: 350px;
    }
    

}


